<template>
  <div class="article-card">
    <div class="card-content">
      <span class="category-tag">{{ article.category.name }}</span>
      <h3 class="card-title">{{ article.title }}</h3>
      <p class="card-meta">
        Oleh {{ article.author.name }} pada {{ formattedDate }}
      </p>
      <p class="card-excerpt">
        {{ article.content.substring(0, 120) }}...
      </p>
      <a href="#" class="read-more-btn">Baca Selengkapnya â†’</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArticleCard',
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  computed: {
    formattedDate() {
      const options = { year: 'numeric', month: 'long', day: 'numeric' };
      return new Date(this.article.date_posted).toLocaleDateString('id-ID', options);
    },
  },
};
</script>

<style scoped>
.article-card {
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0,0,0,0.1), 0 2px 4px -1px rgba(0,0,0,0.06);
  transition: transform 0.3s, box-shadow 0.3s;
}
.article-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px -3px rgba(0,0,0,0.1), 0 4px 6px -2px rgba(0,0,0,0.05);
}
.card-content {
  padding: 24px;
}
.category-tag {
  display: inline-block;
  background-color: #ffeff7;
  color: #ff69b4;
  padding: 4px 12px;
  border-radius: 9999px;
  font-size: 0.8em;
  font-weight: bold;
}
.card-title {
  margin-top: 16px;
  margin-bottom: 8px;
  font-size: 1.25em;
}
.card-meta {
  font-size: 0.9em;
  color: #718096;
  margin-bottom: 16px;
}
.card-excerpt {
  color: #4a5568;
}
.read-more-btn {
  display: inline-block;
  margin-top: 16px;
  font-weight: bold;
  color: #ff69b4;
  text-decoration: none;
}
</style>