<template>
  <div class="page-container">
    <h1>Semua Artikel</h1>
    <p>Temukan informasi dan tips terbaru seputar dunia kehamilan.</p>

    <div v-if="status === 'loading'" class="loading">Memuat...</div>
    <div v-if="status === 'error'" class="error">Gagal memuat artikel.</div>

    <div v-if="status === 'success'" class="article-grid">
      <ArticleCard v-for="article in allArticles" :key="article.id" :article="article" />
    </div>
  </div>
</template>

<script setup>
import { computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import ArticleCard from '@/components/articles/ArticleCard.vue';

const store = useStore();

// Menggunakan computed property untuk mendapatkan data dari Vuex store
const allArticles = computed(() => store.getters['articles/allArticles']);
const status = computed(() => store.state.articles.status);

// Panggil action fetchArticles saat komponen di-mount
onMounted(() => {
  if (allArticles.value.length === 0) {
    store.dispatch('articles/fetchArticles');
  }
});
</script>

<style scoped>
.page-container {
  max-width: 1100px;
  margin: 40px auto;
  padding: 0 20px;
}
h1 { text-align: center; }
p { text-align: center; margin-bottom: 40px; color: #666; }
.article-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}
</style>